@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswaldmedium.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/oswaldregular.woff2") format("woff2"), url("../../fonts/oswaldregular.woff") format("woff");
}
.body {
  margin: 0 auto;
}
.wrapper {
  width: 280px;
  margin: 0 auto;
}
* {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  .wrapper {
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    width: 1220px;
  }
}
.header {
  border-bottom: 1px solid #D9D9D9;
}
.header__wrapper {
  width: 280px;
  margin: 0 auto;
  padding-bottom: 12px;
}
.header-logo {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  text-align: center;
}
.header-logo__link:hover {
  opacity: 0.8;
}
.header-logo__link:active {
  opacity: 0.6;
}
.header-nav {
  display: grid;
  align-items: center;
}
.header-logo__button {
  position: relative;
  z-index: 20;
  top: 8px;
  left: 2px;
  width: 0;
  height: 0;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.header-logo__button::before {
  position: absolute;
  content: "";
  top: 4px;
  right: 1px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.header-logo__button--closed {
  position: relative;
  z-index: 20;
  top: 8px;
  left: 2px;
  width: 0;
  height: 0;
  cursor: pointer;
  border: none;
}
.header-logo__button--closed::after {
  position: absolute;
  content: "";
  top: 10px;
  right: 1px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  transform: rotate(45deg);
}
.header-logo__button--closed::before {
  position: absolute;
  content: "";
  top: 10px;
  right: 1px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  transform: rotate(-45deg);
}
.header-nav--closed .header-nav__item {
  display: none;
}
.header-nav__list {
  padding: 0;
  margin: 0;
}
.header-nav__item {
  margin: 0 auto;
  text-align: center;
  list-style: none;
  padding: 21px 0;
  border-top: 1px solid #e7e7e7;
}
.header-nav__link,
.header-nav__link--index {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 768px) {
  .header {
    width: 708px;
    margin: 0 auto;
    padding-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border: none;
  }
  .header__wrapper {
    width: 174px;
    margin: 0;
    margin-top: 1px;
  }
  .header-nav,
  .header-nav--index {
    margin-top: -10px;
    width: 440px;
    display: flex;
    flex-wrap: wrap;
  }
  .header-nav__list {
    width: 440px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .header-logo__button {
    display: none;
  }
  .header-nav__item {
    width: auto;
    margin: 0;
    padding: 0;
    border: none;
  }
  .header-nav__link,
  .header-nav__link--index {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
  .header-nav__link:hover,
  .header-nav__link--index:hover {
    opacity: 0.6;
  }
  .header-nav__link:active,
  .header-nav__link--index:active {
    opacity: 0.3;
  }
  .header-nav__link--active {
    padding-bottom: 6px;
    border-bottom: 2px solid #68B738;
  }
  .header-nav--closed .header-nav__item,
  .header-nav--opened .header-nav__item {
    display: block;
  }
}
@media (min-width: 1440px) {
  .header {
    width: 1220px;
    margin: 0 auto;
  }
  .header__wrapper {
    margin-top: 0;
  }
  .header-nav,
  .header-nav--index {
    z-index: 10;
    margin-top: -16px;
  }
  .header-nav__list {
    width: 483px;
    padding-left: 3px;
    padding-top: 3px;
    display: flex;
    justify-content: space-between;
  }
  .header-nav__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
  .header-nav__link--index {
    color: #ffffff;
  }
  .header-nav--index .header-nav__link--active {
    border-bottom: 2px solid #ffffff;
  }
}
.footer {
  min-width: 320px;
  background-color: #f2f2f2;
  text-align: center;
}
.footer__wrapper {
  display: grid;
  grid-template-columns: 280px;
}
.footer__logo {
  padding: 35px 0 27px;
  border-bottom: 1px solid #e7e7e7;
}
.footer__logo:hover {
  opacity: 0.8;
}
.footer__logo:active {
  opacity: 0.6;
}
.footer-social__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 22px;
  margin-left: 33px;
  margin-right: 78px;
}
.footer-social__item {
  list-style: none;
}
.footer-social__link {
  fill: #666666;
  display: block;
}
.footer-social__link .footer-social__icon--facebook {
  stroke: #666666;
}
.footer-social__link:hover .footer-social__icon--vkontakte {
  fill: #68B738;
}
.footer-social__link:hover .footer-social__icon--instagram {
  fill: #68B738;
}
.footer-social__link:hover .footer-social__icon--facebook {
  stroke: #68B738;
}
.footer-social__link:active .footer-social__icon--vkontakte {
  opacity: 0.3;
  fill: #68B738;
}
.footer-social__link:active .footer-social__icon--instagram {
  opacity: 0.3;
  fill: #68B738;
}
.footer-social__link:active .footer-social__icon--facebook {
  opacity: 0.3;
  stroke: #68B738;
}
.footer-copyright__link {
  padding: 22px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #e7e7e7;
  text-decoration: none;
}
.footer-copyright__text {
  margin: 0;
  font-family: "Arial", "Helvetica", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
}
.footer-copyright__image {
  fill: #666666;
}
.footer-copyright__link:hover .footer-copyright__image {
  fill: #68B738;
}
.footer-copyright__link:active .footer-copyright__text,
.footer-copyright__link:active .footer-copyright__image {
  opacity: 0.3;
  fill: #68B738;
}
@media (min-width: 768px) {
  .footer__wrapper {
    padding: 30px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .footer__logo {
    padding: 27px 0;
    border: none;
  }
  .footer-social__list {
    width: 130px;
    padding: 0;
    margin-right: 10px;
  }
  .footer-social__icon--vkontakte {
    width: 30px;
    height: 22px;
  }
  .footer-social__icon--instagram {
    width: 23px;
    height: 23px;
  }
  .footer-social__icon--facebook {
    width: 28px;
    height: 33px;
  }
  .footer-copyright {
    width: 163px;
    border: none;
  }
}
@media (min-width: 1440px) {
  .footer__wrapper {
    width: 1220px;
  }
  .footer__wrapper .footer__logo {
    padding-left: 0;
  }
  .footer__wrapper .footer-social__list {
    margin-top: 22px;
  }
  .footer__wrapper .footer-copyright {
    padding: 0;
  }
}
